import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSpinner, faCircle } from '@fortawesome/free-solid-svg-icons'

function Input(props)
{
  return (
    <div className="field">
      <p className={"control " + (props.icon ? "has-icons-right" : "")}>
        <input 
          className={"input is-small " + props.addClass + " " + (props.type === "number" ? "has-text-right" : "")} 
          onChange={props.onChange} 
          value={props.value} 
          disabled={props.disabled}
          type={props.type === "number" ? "text" : props.type }     
          inputMode={ props.type === "number" ? "numeric" : "text" } 
          pattern = { props.type === "number" ? "[0-9]+([,.][0-9]+)?" : ".*" }
          placeholder={props.placeholder} 
          name={props.name}
          onKeyUp={props.onKeyUp}
          onBlur={props.onBlur}
          onKeyDown={props.onKeyDown}
          list={props.list}
          max={props.max}
          size={props.size}
          readOnly={props.readOnly} />
        { props.icon &&
          <span className="icon is-small  is-right">
            <FontAwesomeIcon icon={props.icon} />
          </span>
        }
        {props.info}
      </p>
    </div>
  );
}

const IsActiveContext = React.createContext({ menu_is_active : false, toggleActive: () => {}  });

class NavBar extends React.Component
{
  constructor(props)
  {
    super(props);
    this.state = { menu_is_active  : false };
  }

  menuOnClick = () => {
    this.setState(state => ({
      menu_is_active: !state.menu_is_active
    }));
  }

  render(){
    return(
      <nav className="navbar is-fixed-top is-primary">
        <div className="container">
          <IsActiveContext.Provider value={{menu_is_active: this.state.menu_is_active, toggleActive : this.menuOnClick }}>
            {this.props.children}
          </IsActiveContext.Provider>
        </div>
      </nav>
    );
  }
}

function NavBarBrand(props){
    return <IsActiveContext.Consumer>
            { ({menu_is_active, toggleActive}) =>
              <div className="navbar-brand">
                {props.children}
                { ! props.noBurger &&
                <div role="button" className={"navbar-burger burger" + ( menu_is_active === true ? " is-active" : "" ) } aria-label="menu" aria-expanded="false" data-target="navbarMenu_" onClick={toggleActive}>
                  <span aria-hidden="true"></span>
                  <span aria-hidden="true"></span>
                  <span aria-hidden="true"></span>
                </div> }
              </div> }
          </IsActiveContext.Consumer>;
}

function NavBarMenu(props){
    return <IsActiveContext.Consumer>
            { value =>
              <div className={"navbar-menu" + ( value.menu_is_active === true ? " is-active" : "" ) } id="navbarMenu_">
                {props.children}
              </div> }
          </IsActiveContext.Consumer>;
}

function Cargando(props){
  return (
  <div className="cargando-container">
    <div className="">
      <div className="fa-layers fa-fw">
        <FontAwesomeIcon icon={faCircle} transform="grow-8"/>
        <FontAwesomeIcon icon={faSpinner} spin inverse />
      </div>  
      <span> {props.text ? props.text : "Cargando"} ...</span>
    </div>
  </div>);
}

function ShowDato(props)
{
  return   <div className="column">
            <div>
              <p className="heading">{props.heading}</p>
              <p className={"subtitle " + props.color}>{props.children}</p>
            </div>
          </div>;
}

function ShowInput(props)
{
  return    <div className={"entrada-dato " + props.className}>
              <p className={"heading is-size-7 " + props.headingClass}>{props.heading}</p>
              <div className={"subtitle " + props.color}>{props.children}</div>
            </div>;
}


class DataEpysa
{
  constructor(param_data_url)
  {
    if(param_data_url)
      this.dataUrl = param_data_url;
    else
      this.dataUrl = "http://sienet.epysa.cl/sie-android/Scripts/Inc/data.asp";

    this.ef_empty_nulls 	= true;
    this.controller       = null;
  }

  abort = () => { if(this.controller) this.controller.abort();}

  set data_url(param_data_url)
  {
    this.dataUrl = param_data_url;
  }

  set emptyNulls(true_false)
  {
    this.ef_empty_nulls = ( true_false ?  true : false )
  }

  comillas(parameters)
  {
  	for (var param in parameters)
  	{

  		if ( typeof parameters[param] === 'string' && ( parameters[param] === "" || parameters[param] === null ) && this.ef_empty_nulls  )
  			parameters[param] = "null";
  		else if( typeof parameters[param] === 'string' && parameters[param].toLowerCase()  !== "null" )
  			parameters[param] = "'" + parameters[param] + "'";
  	}
  }

  async exec(procedure, parameters)
  {
    let param = "";

    this.controller = new AbortController();

    if (parameters === null || parameters === undefined)
      parameters = {};

  	this.comillas(parameters);

  	parameters.procedure = "EXEC " + procedure;

    for (let par in parameters)
    {
      const value = parameters[par] === null || parameters[par] === undefined ? "null" : parameters[par] 
      param  = param + par + "=" + value + "&";
    }

  	return await fetch( this.dataUrl,
        {
  				method 	: 'POST',
          headers : { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' },
          body    : encodeURI(param),
          signal  : this.controller.signal
  			});
  }

}

function toMoney(n){
  if (n || n === 0)
    if ( isNaN(toNumber(n)) )
      return n;
    else
      return Number(toNumber(n).toFixed(0)).toLocaleString();
  else 
    return "";
}

function toNumber(str){

  let result;
  ////console.log("IN: " + str + " " + typeof str)
  if (typeof str === "string" )
  {
    const str_limpio  = str.replace( /,/g, "" )
    if (! isNaN(str_limpio) && str.slice(-1) !== "." && str.length > 0 ){
      const numero = Number( str_limpio );
      result = isNaN(numero) ? str : numero;
    } 
    else
      result = str;
  }
  else result = str;
  ////console.log("OUT: " + result + " " + typeof result)
  return result;
}

function toBinaryHex(str)
{
  return str.split("").reduce((hex,c)=>hex+=c.charCodeAt(0).toString(16).padStart(4,"0"),"")
}

function percent(n)
{
  if (n)
    if ( typeof toNumber(n) !== "number" )
      return n;
    else
      return Number( Number(n).toFixed(2)  );
  else 
    return "";
}


function string_time(hr, mn)
{
  const horas   = hr ? hr : 0;
  const minutos = mn ? mn : 0;
  return horas + ":" + ( minutos < 10 ? "0" +  minutos : minutos);
}
  

export { Input, NavBar, NavBarBrand, DataEpysa, NavBarMenu, toMoney, Cargando, toBinaryHex, ShowDato, ShowInput, percent, toNumber, string_time}
