import React from 'react';
import './App.sass';
import { Input, NavBar, NavBarBrand, DataEpysa } from './utils.js';
import { Redirect } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faKey } from '@fortawesome/free-solid-svg-icons'
import Cookies from 'universal-cookie';
import md5 from 'md5';

class Login extends React.Component
{
    constructor(props)
    {
      super(props);
      this.data_source = new DataEpysa('http://sienet.epysa.cl/sie-android/Inc/data_ex.asp?DB_EMP=SERVIBUS');
      this.cookies = new Cookies();
      this.state =
      {
        error       : null,
        redirect    : 0,
        usuario     : this.props.wv_id ? this.props.wv_id : "",
        isLoading   : false,
        password    : this.props.wv_ps ? this.props.wv_ps : "",
        wv_login    : 0
      }
    }

    setValues = (event) =>
    {
        const target = event.target;
        this.setState( { [target.name] : target.value } );
    }

    componentDidMount(){
      //console.log( "autologin?: " +  this.props.wv_id  );
      if(this.props.wv_id)
        this.tryLogin();
    }

    tryLogin = () =>
    {
      this.setState({isLoading : true});
      //console.log( this.props.wv_ps );
      this.data_source.exec(
        'USR_LOGIN',
        { nombre_usuario  : this.state.usuario,
          contrasena      : this.props.wv_id ? this.state.password : md5(this.state.password)
        }
      )
        .then (res => res.json())
        .then (
          result => {
            //console.log(result.data);
            if(result.data[0].estado === "ok")
            {

              const info = result.data[0];
              let name = info.nombre.split(" ")[0] + " " + info.apellidos.split(" ")[0]


              let expiryDate = new Date();
              expiryDate.setMonth(expiryDate.getMonth() + 6);

              this.cookies.set("login_state", true, { path: '/', expires : expiryDate  });
              this.cookies.set("user", this.state.usuario, { path: '/', expires : expiryDate  });
              this.cookies.set("nombre", name, { path: '/', expires : expiryDate  });
              this.setState({ redirect : 1, isLoading  : false });
            }
            else
              this.setState({ error : "badUP", isLoading  : false });              
          },
          error => {
            this.setState({
              isLoading  : false,
              error
            });
          }
        );
    }

    onEnter = (event) => {
      if(event.keyCode === 13)
        this.tryLogin();
    }

    render()
    {
      const { error, isLoading, redirect, usuario, password } = this.state;
      let envia_enabled = usuario && password;
    
      if (redirect === 1){
        let redirect = <Redirect to="/" />;
        
        if(this.props.id)
          redirect = <Redirect to={"/enegocio?id=" + this.props.id} />;
        else if ( this.props.cl)
          redirect = <Redirect to={"/?cl=" + this.props.cl} />;
        
          return redirect;
      }
      else if (error && error !== "badUP")
        return <div className="app-error is-">Error: {error.message}</div>;
      else
        return (
          <>
          <NavBar>
            <NavBarBrand noBurger>
              <div className="navbar-item" >
                <div className="padding-icon"><FontAwesomeIcon size="lg" icon={faUser}/></div> Iniciar Sesión
              </div>
            </NavBarBrand>
          </NavBar>
          <section className="section">
              <div className="container-login">
                <div className="box">
                    <Input name="usuario" onChange={this.setValues} icon={faUser} placeholder="Nombre de usuario" type="text" />
                    <Input name="password" onChange={this.setValues} icon={faKey} placeholder="Contraseña" type="password" onKeyUp={this.onEnter} />
                    {error === "badUP" && <p className="has-text-danger">*Nombre de usuario y/o contraseña incorrectos.</p> }
                    <div className="modal-buttons"> 
                      <button className="button is-danger is-small" onClick={this.tryLogin}>Cancelar</button>
                      <button className={"button is-primary is-small " + ( isLoading && "is-loading" )} onClick={this.tryLogin} disabled={ !envia_enabled }>Aceptar</button>
                    </div>
                </div>
              </div>
          </section>
          </>
        );
    }
} 

export default Login;