import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Login from './Login';
import Agregar from './Agregar';

import {
  BrowserRouter as Router,
  Redirect,
  Switch,
  Route
} from "react-router-dom";
import Cookies from 'universal-cookie';




export default function Main(props) {
  const cookies = new Cookies();
  const urlParams = new URLSearchParams(window.location.search);
  const wv_id = urlParams.get('wv_id') ? urlParams.get('wv_id') : null;
  const wv_ps = urlParams.get('wv_ps') ? urlParams.get('wv_ps') : null;
  

  let redirect;
  
  if ( cookies.get("login_state") ){
    let expiryDate = new Date();
    expiryDate.setMonth(expiryDate.getMonth() + 6);
    cookies.set("login_state", true, { path: '/', expires : expiryDate  });
    cookies.set("user", cookies.get("user"), { path: '/', expires : expiryDate  });
    cookies.set("nombre", cookies.get("nombre"), { path: '/', expires : expiryDate  });
  }
  else
    redirect = <Redirect to="/login" />;
  
  return (
    <Router basename="/bitacora_horas_extra/">
        <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/login">
            <Login wv_id={wv_id} wv_ps={wv_ps} />
          </Route>
          <Route path="/agregar">
            <Editar />
          </Route>
          <Route path="/">
            <App />
          </Route>
        </Switch>
      {redirect}
    </Router>
  );
}

function About() {
  return <h2>Version 1.1</h2>;
}

function Editar(props)
{
  const urlParams = new URLSearchParams(window.location.search);
  const r         = urlParams.get('rhe') ?  urlParams.get('rhe') : ''  ;
  const cookies = new Cookies();

  return <Agregar user={cookies.get("user")} rhe={r} />
}


ReactDOM.render(
  <React.StrictMode>
    <Main />
  </React.StrictMode>,
  document.getElementById('root')
);
