import React from 'react';
import './App.sass';
import { Link, Redirect } from "react-router-dom";
import { Input, NavBar, NavBarBrand, NavBarMenu, DataEpysa, Cargando, string_time } from './utils.js';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch, faPlus, faClock } from '@fortawesome/free-solid-svg-icons';
import Cookies from 'universal-cookie';

class App extends React.Component
{
  constructor(props)
  {
    super(props);
    this.data_source = new DataEpysa('http://sienet.epysa.cl/sie-android/Inc/data_ex2.asp?DB_EMP=SERVIBUS');
    this.cookies = new Cookies();
    this.state =
    {
      error         : null,
      data          : [],
      isLoaded      : false,
      redirect      : null,
      filtro        : this.props.cliente ? this.props.cliente : '',
      filtro_estado : ''
    }
  }

  setFitlro = (e) =>{
    this.setState({
      filtro : e.target.value
    });
  }

  openNegocio = (id) => { console.log(id) }

  componentDidMount()
  {
    this.data_source.exec(
      'RHE_LISTA',
      { empleado_responsable : this.cookies.get("user") }
    )
      .then (res => res.json())
      .then (
        result => {
          //console.log(result.data);
          this.setState({
            isLoaded  : true,
            data      : result.data ? result.data : []
          });
        },
        error => {
          this.setState({
            isLoaded  : true,
            error
          });
        }
      );
  }

  cerrarSesion = () => {
    this.cookies.remove("login_state", { path: '/'});
    this.cookies.remove("user", { path: '/'});
    this.cookies.remove("nombre", { path: '/'});
    this.setState({redirect : "logout"});
  }

  render()
  {
    const { error, data, isLoaded, redirect, filtro, filtro_estado } = this.state;
    if (error)
      return <div className="app-error">Error: {error.message}</div>;
    if (redirect === "logout")      
      return <Redirect to="/login" />;
    else if (!isLoaded)
      return <Cargando />
    else
      return (
        <>
        <NavBar>
          <NavBarBrand>
            <div className="navbar-item" >
              <span className="icon is-medium">
                <FontAwesomeIcon size="lg" icon={faClock}/>
              </span>
              <span>Horas Extra</span>
            </div>
            <div className="navbar-item" >
              <Input onChange={this.setFitlro} size="16" icon={faSearch} placeholder="Buscar" type="text" />
            </div>
          </NavBarBrand>
          <NavBarMenu closeOnClick="true" >
            <div className="navbar-end">              
              <div className="navbar-item has-dropdown is-hoverable">
                <p className="navbar-link">{ this.cookies.get("nombre") }</p>
                <div className="navbar-dropdown">
                  <div className="navbar-item">
                    <div className="buttons">
                      <button className="button is-danger is-light is-small" onClick={this.cerrarSesion}>
                        Cerrar Sesion
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              
            </div>
        </NavBarMenu>
        </NavBar>
        <section className="section">
          <div className="container">
            <ItemList data={data} onClick={this.openNegocio} filtro={filtro} filtroEstado={filtro_estado}/>
          </div>
            
        </section>
        <div className="is-fab has-content-centered">
          <Link to="/agregar" >
            <button className="button is-primary is-rounded has-shadow">
              <span className="icon">
                <FontAwesomeIcon icon={faPlus} />
              </span>
              <span>Agregar</span>
            </button>
          </Link>
        </div>
        </>
      );
  }
}

function ItemList(props)
{
  const data    = props.data;
  const filtro  = new RegExp( props.filtro.toLowerCase(), "g" );

  
  //const filtro_estado = props.filtroEstado;

  const filtered_data = data.filter(item => ( item.cliente.toLowerCase().match( filtro ) || item.nombre_cliente.toLowerCase().match( filtro ) ));

  const listItems = filtered_data.map( (item, index) =>
    {
      if ( (index === 0 && filtered_data.length > 0 ) || ( index > 0 && filtered_data.length && filtered_data[index-1].fecha_trabajo.substring(0,7) !== item.fecha_trabajo.substring(0,7) ) )
      {
        let horas_total     = 0;
        let minutos_total   = 0;
        let horas_pagadas   = 0;
        let minutos_pagados = 0;

        filtered_data.filter(i => i.fecha_trabajo.substring(0,7) === item.fecha_trabajo.substring(0,7) )
          .map(i => {
            horas_total     = horas_total     + (i.cantidad_horas           ? i.cantidad_horas            : 0);
            minutos_total   = minutos_total   + (i.cantidad_minutos         ? i.cantidad_minutos          : 0);
            horas_pagadas   = horas_pagadas   + (i.cantidad_horas_pagadas   ? i.cantidad_horas_pagadas    : 0);
            minutos_pagados = minutos_pagados + (i.cantidad_minutos_pagados ? i.cantidad_minutos_pagados  : 0);
            return true;
          });

        return  <Item key={item.id}> 
                  <Resumen
                    fecha={item.fecha_trabajo} 
                    horas_total={horas_total} 
                    minutos_total={minutos_total} 
                    horas_pagadas={horas_pagadas} 
                    minutos_pagados={minutos_pagados} />
                  <ListItem item={item} onClick={ () => props.onClick(item.id) }  />
                </Item>
      }
      else
        return ( <Item key={item.id}> <ListItem key={item.id} item={item} onClick={ () => props.onClick(item.id) }  /> </Item> );

    }
  );

  return <div className="app-listado">{listItems}</div>;
      
}

function Item(props)
{
  return props.children;
}

function Resumen(props)
{
  const mes = (new Date(props.fecha)).toLocaleString('es-CL', { month: 'long', year: 'numeric' });
  return  <div className="level is-mobile resumen">
            <div className="level-left">
              <div className="level-item has-text-centered">
                <div>
                  <p className="title">{mes}</p>
                </div>
              </div>
            </div>
            <div className="level-right">
              <div className="level-item has-text-centered">
                <div>
                  <p className="heading">total horas</p>
                  <p className="title">{string_time(props.horas_total, props.minutos_total)}</p>
                </div>
              </div>
              <div className="level-item has-text-centered">
                <div>
                  <p className="heading">total pagadas</p>
                  <p className="title">{string_time(props.horas_pagadas, props.minutos_pagados)}</p>
                </div>
              </div>
            </div>
          </div>;
}


function ListItem(props)
{
  const item          = props.item;
  const fecha_trabajo = new Date( item.fecha_trabajo );
  const fecha_pago    = new Date( item.fecha_pago ); 
  let color_estado = "";

  switch (item.estado) {
    case "ING":
      color_estado = "has-text-info";
      break;
    case "AUT":
      color_estado = "has-text-success";
      break;
    case "REC":
        color_estado = "has-text-danger";
        break;
    default:
      break;
  }

  return  <Link to={"/agregar?rhe=" + item.id} > 
            <div className="list-box" onClick={ props.onClick } >
              <div className="box-row">
                <div className="text-overflow-ltr is-size-7">
                  <strong>{item.cliente} {item.nombre_cliente}</strong>
                </div>
                <div className="is-size-7">
                  <strong className={color_estado}>{item.estado}</strong>
                </div>
              </div>
              <div className="box-row">
                <div className="is-size-7">
                  <strong>{fecha_trabajo.toLocaleDateString("es-ES").replace("/20", "/")}</strong> {item.hora_inicio.replace(":00.0000000","")} a {item.hora_fin.replace(":00.0000000","")} <strong className="has-text-info">+{string_time(item.cantidad_horas, item.cantidad_minutos)}</strong> 
                </div>
                <div className="is-size-7">
                  pagado: <strong className="has-text-primary">{string_time(item.cantidad_horas_pagadas, item.cantidad_minutos_pagados)}</strong> {item.fecha_pago ? fecha_pago.toLocaleString("es-ES", {month : "short"}).replace("/20", "/") : (item.estado === "REC" ? "rchaz." : "pend.")}
                </div>
              </div>
            </div>
          </Link>
}

export default App;
