import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft, faClock, faIndustry, faCalendar, faFile, faBus } from '@fortawesome/free-solid-svg-icons';
import { NavBar, NavBarBrand, DataEpysa, Cargando, ShowInput, Input, string_time } from './utils';
import { Link } from "react-router-dom";

class Agregar extends React.Component
{
  constructor(props)
  {
    super(props);
    this.data_source = new DataEpysa('http://sienet.epysa.cl/sie-android/Inc/data_ex2.asp?DB_EMP=SERVIBUS');
    this.state =
    {
      error           : null,
      isLoaded        : false,
      autoClientes    : [],
      guardado        : false,
      eliminado       : false,
      confirm_delete  : false, 
      dato            : {
        cliente           : "",
        descripcion       : "",
        numero_documento  : "",
        fecha_trabajo     : "",
        hora_inicio       : "",
        hora_fin          : "",
        estado            : "ING",
        diferencia        : "0:00"
      },
    };
  }


  componentDidMount()
  {
    if(this.props.rhe !== "")
    {
      this.data_source.exec(
        'RHE_LISTA',
        { 
          id                    : Number(this.props.rhe),
          empleado_responsable  : this.props.user
        }
      )
        .then (res => res.json())
        .then (
          result => {
  
            if( result.data ){
              const data          = result.data[0]; 
              data.diferencia     = string_time(data.cantidad_horas, data.cantidad_minutos);
              data.cliente        = data.cliente + " " + data.nombre_cliente;
              data.hora_inicio    = data.hora_inicio.replace(":00.0000000","");
              data.hora_fin       = data.hora_fin.replace(":00.0000000","");
              data.fecha_trabajo  = data.fecha_trabajo.split("T")[0];

              this.setState({ isLoaded   : true, dato : data});
            }
            else {
              this.setState({
                error : { message : "error de datos obtenidos"}
              });
            }
              
          },
          error => {
            this.setState({
              error
            });
          }
        );
  
    }
    else 
      this.setState({isLoaded: true});

  }

  eliminaRegistro = () => {
    if (!this.state.confirm_delete)
    {
      this.setState({confirm_delete : true});
    }
    else
    {
      this.setState({isLoaded: false});
      this.data_source.exec(
        'RHE_ELIMINA',
        { 
          id_rhe                : Number(this.props.rhe),
          empleado_responsable  : this.props.user
        }
      )
        .then (res => res.json())
        .then (
          result => {
  
            if( result.data )
              this.setState({ isLoaded   : true, eliminado: true });
            else {
              this.setState({
                error : { message : "error eliminando dato" }
              });
            }
              
          },
          error => {
            this.setState({
              error
            });
          }
        );
    }
    
  }

  guardaDatos = () => {

    this.setState({isLoaded: false});
    const dato        = this.state.dato;
    //const numero_doc  = Number(dato.numero_documento);
    const duracion    = dato.diferencia.split(":");
    this.data_source.exec(
      'RHE_GRABA',
      { ...dato, ...{
        tipo_documento        : 'OVT',
        cantidad_horas        : duracion[0],
        cantidad_minutos      : duracion[1],
        empleado_responsable  : this.props.user,
        cliente               : dato.cliente.split(" ")[0],
        estado                : 'ING'
      }}
    )
      .then (res => res.json())
      .then (
        result => {

          if( result.data )
            this.setState({ isLoaded   : true, guardado: true });
          else {
            this.setState({
              error : { message : "error al grabar datos"}
            });
          }
        },
        error => {
          this.setState({
            error
          });
        }
      );

  }

  getItemsAsync = (value) => {
    if(value.length > 2 ){
      this.data_source.abort();
      this.data_source.exec(
        'EC_AUTOCOMPLETAR',
        { termino : unescape(encodeURIComponent(value)) }
      )
        .then (res => res.json())
        .then (
          result => {
            if(result.data){
              //console.log(result.data);
              this.setState({
                autoClientes  : result.data
              });
            }
            else
              this.setState({
                autoClientes  : []
              });
          }
        );
    }
  }

  onChangeSuggest = event => {
    const target = event.target;
    this.setState(state => {
      const suggestion = state.autoClientes.filter( s => s.label === target.value )
      if ( suggestion.length === 0 )
        this.getItemsAsync(target.value);    
      return { dato : {...state.dato, ...{ cliente : target.value } } };
    });
    
  };

  setData = (event) => {
    const element = event.target;
    let valor = element.value;

    if ( element.name === "numero_documento" )
      if ( element.value !== "") 
        if ( ! isNaN(element.value) )
          valor = Number(element.value)
        else return;
    else
      valor = null;

    //if (element.name === "fecha_trabajo")
      //valor = element.value.replace(/-/g, ""),

    this.setState(state => {
      console.log(element.value);
      const newDato = {...state.dato,...{[element.name] : valor }};
      return { dato : {...newDato,...{ diferencia : this.calcula_diferencia(newDato)}} }
    });

  }

  calcula_diferencia = (dato) =>
  {
    let diferencia = "0:00";
    if (dato.hora_inicio && dato.hora_fin)
    {
      let time_zero_ini = new Date(0);
      let time_zero_fin = new Date(0);
      const time_ini    = dato.hora_inicio.split(":");
      const time_fin    = dato.hora_fin.split(":");

      if (time_ini.length === 2 && time_fin.length === 2)
      {
        time_zero_ini.setHours(time_ini[0]);
        time_zero_ini.setMinutes(time_ini[1]);

        time_zero_fin.setHours(time_fin[0]);
        time_zero_fin.setMinutes(time_fin[1]);        

        if (time_zero_ini > time_zero_fin )
          time_zero_fin.setDate( time_zero_fin.getDate() + 1 )

        const diffTime = Math.abs(time_zero_fin - time_zero_ini);
        const diffTotalMinutes = Math.ceil(diffTime / (1000 * 60))

        const diffHours = Math.floor(diffTotalMinutes / 60 )
        const diffMinutes = Math.floor(diffTotalMinutes % 60 )

        diferencia = string_time(diffHours, diffMinutes);

      }
    }
    return diferencia;
  }

  render()
  {
    const {error, isLoaded, dato, autoClientes, guardado, eliminado, confirm_delete} = this.state;
    const diff      = dato.diferencia; 
    const disabled  = ( dato.cliente === "" || dato.descripcion === "" || dato.fecha_trabajo === "" || dato.hora_inicio === "" || dato.hora_fin === "" );
    const lectura   = ( dato.estado !== "ING" )

    if (error)
      return <div className="app-error">Error: {error.message}</div>;
    else if (!isLoaded)
      return <Cargando />
    else if (guardado  || eliminado)
      return  <div className="absolute-center-80vh">
                <div className="box">
                  <div className="is-block has-text-centered">
                    <p>{guardado ? "Guardado" : "Eliminado"} correctamente</p>
                    <Link to="/" >
                      <button className="button is-primary btn-guardado">Aceptar</button>
                    </Link>
                  </div>
                </div>
              </div>;
    else if (confirm_delete)
      return  <div className="absolute-center-80vh">
                <div className="box">
                  <div className="is-block has-text-centered">
                    <p>Está seguro que desea eliminar ?</p>
                      <button onClick={ () => this.eliminaRegistro() } className="button is-primary btn-guardado">Aceptar</button>
                      <button onClick={ () => this.setState({confirm_delete : false}) } className="button is-danger btn-guardado">Cancelar</button>
                  </div>
                </div>
              </div>;
    else
      return  <>
                <NavBar>
                    <NavBarBrand noBurger={true}>
                      <>
                        <div className="navbar-item" >
                          <Link to="/" className="has-text-white-bis">
                            <FontAwesomeIcon icon={faArrowLeft} size="lg" />
                          </Link>
                        </div>
                        <div className="navbar-item" >
                            <span className="icon is-medium">
                              <FontAwesomeIcon size="lg" icon={faClock}/>
                            </span>
                            <span>Registro Hora Extra</span>
                        </div>
                      </>
                  </NavBarBrand>
                </NavBar>
                <section className="section">
                  <div className="container cuerpo-simple">
                    <ShowInput heading="Cliente">
                      <input 
                        name="cliente" 
                        id="cliente"
                        list="clisuggestions" 
                        className={"input is-small dtlst " + (dato.cliente === "" ? "is-danger" : "")} 
                        value={dato.cliente} 
                        disabled={lectura}
                        type="text"
                        onChange={this.onChangeSuggest} icon={faIndustry} 
                      />
                      <datalist id="clisuggestions">
                        { autoClientes.map( i => <option key={i.value} value={i.label} />) }
                      </datalist> 
                    </ShowInput>
                    <ShowInput heading="Número OT">
                      <Input 
                        name="numero_documento" 
                        value={dato.numero_documento} 
                        onChange={this.setData} 
                        type="number" 
                        disabled={lectura}
                        icon={faFile} 
                      />
                    </ShowInput>
                    <ShowInput heading="Número Carro">
                      <Input 
                        name="identificador_vehiculo" 
                        value={dato.identificador_vehiculo} 
                        onChange={this.setData} 
                        type="number" 
                        disabled={lectura}
                        icon={faBus} 
                      />
                    </ShowInput>
                    <ShowInput heading="Descripción trabajo">
                      <textarea 
                        name="descripcion" 
                        className={"textarea is-small " + (dato.descripcion === "" ? "is-danger" : "")} 
                        value={dato.descripcion} 
                        onChange={this.setData} 
                        rows="2"
                        disabled={lectura} 
                      />
                    </ShowInput>
                    <ShowInput heading="Fecha trabajo">
                      <Input 
                        name="fecha_trabajo" 
                        addClass={dato.fecha_trabajo === "" ? "is-danger" : ""} 
                        value={dato.fecha_trabajo} 
                        onChange={this.setData} 
                        disabled={lectura}
                        type="date"
                        max={(new Date()).toISOString().split("T")[0]} 
                        icon={faCalendar} 
                      />
                    </ShowInput>
                    <ShowInput heading="Hora desde">
                      <Input 
                        name="hora_inicio" 
                        addClass={dato.hora_inicio === "" ? "is-danger" : ""} 
                        value={dato.hora_inicio} 
                        onChange={this.setData} 
                        disabled={lectura}
                        type="time" 
                        icon={faClock} 
                      />
                    </ShowInput>
                    <ShowInput heading="Hora hasta">
                      <Input 
                        name="hora_fin" 
                        addClass={dato.hora_fin === "" ? "is-danger" : ""} 
                        value={dato.hora_fin} 
                        onChange={this.setData} 
                        disabled={lectura}
                        type="time" 
                        icon={faClock} 
                      />
                    </ShowInput>
                  </div>
                  <div className="container cuerpo-simple">
                    <div className="duracion"> 
                      <p className="heading">Duración</p>
                      <p className="subtitle has-text-primary has-text-centered">{diff}</p>
                    </div>
                  </div>
                  <div className="container cuerpo-simple">
                    <div className="buttons has-text-centered footer-buttons">
                      { !lectura && this.props.rhe && 
                        <button className="button is-danger" onClick={() => this.eliminaRegistro()}>
                          Eliminar
                        </button>
                      }
                      { !lectura &&
                        <button className="button is-primary" disabled={disabled} onClick={() => this.guardaDatos()}>
                          Guardar
                        </button>
                      }
                    </div>
                  </div>
                </section>
              </>;
  }
}

export default Agregar